.Main {
  height: 1px;
  min-height: 100%;
  display: flex;
  flex-direction: column; }

.Main__content {
  flex: 1 1 0;
  display: flex;
  align-items: center;
  justify-content: center; }

.Main__get-started {
  background-color: #fff;
  border: 1px solid #ae1903;
  color: #ae1903;
  border-radius: 4px;
  outline: none;
  transition: background-color .2s ease-in, border-color .2s ease-in, color .2s ease-in;
  padding: .5em 1em;
  cursor: pointer;
  font-size: 1.5em;
  text-decoration: none; }
  .Main__get-started:hover, .Main__get-started:focus, .Main__get-started:active {
    text-decoration: none;
    color: #ae1903; }
  .Main__get-started:hover {
    background-color: #ae1903;
    border-color: #fff;
    color: #fff; }
    .Main__get-started:hover:visited, .Main__get-started:hover:active {
      text-decoration: none;
      color: #fff; }
