.Nav {
  flex: 1;
  align-self: stretch;
  display: flex;
  justify-content: flex-end;
  align-items: stretch; }

.Nav__item {
  padding: 1em;
  transition: background-color 0.2s ease-out;
  cursor: pointer; }
  .Nav__item:hover {
    background-color: #951503; }

.Nav__menu {
  display: flex;
  flex-direction: column; }

.Nav__menu-item {
  padding: 1em;
  transition: background-color 0.2s ease-out; }
  .Nav__menu-item:hover {
    background-color: #951503; }

.Nav__link {
  display: flex;
  align-items: center;
  color: #fff;
  text-decoration: none;
  white-space: nowrap; }
  .Nav__link:hover, .Nav__link:focus, .Nav__link:visited, .Nav__link:active {
    color: #fff;
    text-decoration: none; }
