/**
 * Возвращает указанное значение из текущей используемой темы.
 * Например:

 // пусть есть такая тема:
 $theme: (
    font-size: 16px;
    button: (
        background-color: #fff,
        border: (
            type: solid,
            thickness: 1px,
            color: #fff,
        ),
        color: #000,
    )
 );

 // в scss-файле, в котором мы хотим использовать тему, пишем вот так:
 @import '../theme/index';

 .some-class {
    background-color: themed('button.background-color');
    border: themed('button.border.type') themed('button.border.thickness') themed('button.border.color');
    color: themed('button.color');
    font-size: themed('font-size');
 }

 */
@keyframes pulse {
  0% {
    transform: scale(1) rotate(0deg); }
  16.665% {
    transform: scale(1.2) rotate(180deg); }
  33.33% {
    transform: scale(1) rotate(360deg); }
  100% {
    transform: scale(1) rotate(360deg); } }

@keyframes change-fill {
  0% {
    fill: #fff; }
  16.665% {
    fill: #ff7e5a; }
  33.33% {
    fill: #fff; }
  100% {
    fill: #fff; } }

.Header__link {
  color: #fff;
  text-decoration: none; }
  .Header__link:active, .Header__link:focus, .Header__link:visited, .Header__link:hover {
    color: #fff;
    text-decoration: none; }

.Header__logo {
  width: 5em;
  fill: #fff; }
  .Header__logo g {
    fill: #fff; }

.Header__title {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: .5em 0 0 0; }

.Header__version {
  font-size: 0.4em;
  font-weight: normal; }

.Header {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2em 0;
  background: #ae1903;
  color: #fff;
  font-size: 2em; }
  .Header--minimize {
    padding: 0 1em;
    font-size: 1em;
    flex-direction: row; }
    .Header--minimize .Header__title {
      flex-direction: row;
      align-items: baseline;
      margin: 0; }
    .Header--minimize .Header__version {
      margin-left: 1em;
      font-size: 0.5em; }
  .Header--animated .Header__logo {
    animation-name: pulse;
    animation-timing-function: linear;
    animation-delay: 10s;
    animation-duration: 13s;
    animation-iteration-count: infinite; }
    .Header--animated .Header__logo g {
      animation-name: change-fill;
      animation-timing-function: linear;
      animation-delay: 10s;
      animation-duration: 13s;
      animation-iteration-count: infinite; }
